html, body, div, span, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
abbr, address, cite, code,
del, dfn, em, img, ins, kbd, q, samp,
small, strong, sub, sup, var,
b, i,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, figcaption, figure,
footer, header, hgroup, menu, nav, section, summary,
time, mark, audio, video {
  margin:0;
  padding:0;
  border:0;
  outline:0;
  font-size:100%;
  vertical-align:baseline;
  background:transparent;
}

h1, h2, h3, h4, h5, h6, p {
  font-weight: normal;
  display: inline-block;
}

body {
  font-family: "Favorit Regular", sans-serif;
}

@font-face {
  font-family: "Favorit Regular";
  src: url("./assets/fonts/Favorit-Regular.woff") format("woff");
}

@font-face {
  font-family: "Spectral Light";
  src: url("./assets/fonts/Spectral-Light.ttf") format("ttf");
}

span.line-break {
  display: block;
}

@media only screen and (min-width: 540px) {
  span.line-break {
    display: inline;
  }
}

.PrimaryNav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding: 0 3.3%;
  box-sizing: border-box;
  position: fixed;
  height: 60px;
  top: 0;
  transition: top 200ms ease-in;
  background: white;
  z-index: 999;
}

.PrimaryNavLink {
  font-size: 40px;
  text-transform: uppercase;
  color: black;
  text-decoration: none;
}

h1 {
  font-size: 42px;
}

h2 {
  font-size: 26px
}

h3 {
  font-size: 20px
}

p, label, input {
  font-size: 16px
}

@media only screen and (min-width: 375px) {
  h1 {
    font-size: 47px;
  }

  h2 {
    font-size: 29px;
  }

  h3 {
    font-size: 23px;
  }

  p, label, input {
    font-size: 18px;
  }
}

@media only screen and (min-width: 480px) {
  h1 {
    font-size: 52px;
  }

  h2 {
    font-size: 32px;
  }

  h3 {
    font-size: 25px;
  }

  p, label, input {
    font-size: 20px;
  }
}

@media only screen and (min-width: 540px) {
  h1 {
    font-size: 58px;
  }

  h2 {
    font-size: 36px;
  }

  h3 {
    font-size: 28px;
  }

  p, label, input {
    font-size: 22px;
  }
}

@media only screen and (min-width: 768px) {
  h1 {
    font-size: 63px;
  }

  h2 {
    font-size: 63px;
  }

  h3 {
    font-size: 39px;
  }

  p, label, input {
    font-size: 24px;
  }
}

@media only screen and (min-width: 992px) {
  h1 {
    font-size: 68px;
  }

  h2 {
    font-size: 68px;
  }

  h3 {
    font-size: 54px;
  }

  p, label, input {
    font-size: 26px;
  }
}

@media only screen and (min-width: 1024px) {
  h1 {
    font-size: 73px;
  }

  h2 {
    font-size: 74px;
  }

  h3 {
    font-size: 58px;
  }

  p, label, input {
    font-size: 28px;
  }
}