.PrimaryNav {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    padding: 0 3.3%;
    box-sizing: border-box;
    position: fixed;
    height: 60px;
    top: 0;
    transition: top 200ms ease-in;
    background: white;
    z-index: 999;
}

.PrimaryNavLink {
    text-transform: uppercase;
    color: black;
    text-decoration: none;
}

.PrimaryNavBtn {
    display: block;
    margin: 0;
    padding: 0;
    outline: none;
    border: none;
    background: white;
}

