.Footer {
    padding: 20px 3.3% 0;
    border-top: solid 1px #cbcbcb;
}

.FooterSignUpLabel {
    display: block;
    padding-bottom: 10px;
    text-transform: uppercase;
}

.FooterSignUp {
    display: block;
    padding: 5px 10px;
    box-sizing: border-box;
    border: solid 1px black;
    width: 100%;
}

.FooterSignUp::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: #cbcbcb;
    opacity: 1; /* Firefox */
}

.FooterSignUp:-ms-input-placeholder { /* Internet Explorer 10-11 */
    color: #cbcbcb;
}

.FooterSignUp::-ms-input-placeholder { /* Microsoft Edge */
    color: #cbcbcb;
}

.FooterTextContainer {
    display: flex;
    justify-content: space-between;
}

@media only screen and (min-width: 768px) {
    .Footer {
        background: rgb(220,226,33);
        background: linear-gradient(90deg, rgba(220,226,33,1) 0%, rgba(38,218,216,1) 100%);
        border-top: none;
        padding: 10px 3.3%;
    }

    .FooterSignUpLabel, .FooterSignUp {
        display: none;
    }

    .FooterTextContainer {
        text-transform: uppercase;
    }
}